<template>
   <div class="buy">
         <van-list
         v-model="loading"
         :finished="finished"
         finished-text="没有更多了"
         @load="onLoad">
            <cargo :data='Data' :sort='Sort' :take='take'></cargo>
         </van-list>
   </div>
</template>

<script>

import cargo from './Cargo'

export default {
   name: 'buy',
   components:{ cargo },
   props:['take'],
    data() {
       return {
         Sort:{ title:'求购' ,judge:'2'},
         Data:[],
         list:[],
         loading: false,
         finished: false,
       }
   },
   watch:{
        take(news,old){
            this.page = 1;
            this.finished = false;
            this.list = [];
        }
   },
   methods:{
       getData(){
           let url = '/otc/buyerPendDetail';
           this.$http.post(url,{
              cmd:'205',
              body:{
                  page:this.page++,
                 type:this.take,
                 accessToken:localStorage.token
              }
           }).then( (res) => {
              this.Data = res;
            //    console.log(this.Data)
           } )
       },
       onLoad() {
           this.getData();
            // 异步更新数据
            setTimeout(() => {
                for (let i = 0; i < this.Data.length; i++) {
                    this.list.push(this.Data[i]);  
                }
                // 加载状态结束
                this.loading = false;
                // 数据全部加载完成
                if (this.Data.length < 10) {
                    this.finished = true;
                }
            }, 500);
        },
   },
}
</script>

<style scoped lang='scss'>
.buy{
    width: 100%;
    height: 100%;
    
}
</style>
