<template>
   <div class="sell">
        <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad">
            <cargo :data='list' :sort='Sort' :take='take'></cargo>
        </van-list> 
    </div>
</template>

<script>
import cargo from './Cargo'

export default {
   name: 'sell',
   components:{ cargo },
   props:['take'],
   data() {
       return {
        Sort:{ title:'出售' , judge:'1'},
        page:1,
        Data:[],
        list:[],
        loading: false,
        finished: false,
       }
   },
   watch:{
       take(news,old){
            this.page = 1;
            this.finished = false;
            this.list = [];
       }
   },
   methods:{
       getData(){
        //    console.log(this.take)
           let url = '/otc/sellerPendDetail';
           this.$http.post(url,{
               cmd:'204',
               body:{
                   page:this.page++,
                   type:this.take,
                   accessToken:localStorage.token
               }
           }).then( (res) =>{
               this.Data = res;
            //    console.log(this.Data);
           })
       },
       onLoad() {
            this.getData();
            // 异步更新数据
            setTimeout(() => {
                for (let i = 0; i < this.Data.length; i++) {
                    this.list.push(this.Data[i]);  
                }
                // 加载状态结束
                this.loading = false;
                // 数据全部加载完成
                if (this.Data.length < 10) {
                    this.finished = true;
                }
            }, 500);
        },
   },
}
</script>

<style scoped lang='scss'>
.sell{
    width: 100%;
    height: 100%;
}
</style>
