<template>
   <div class="deal">
        <div class='mine_top'>
            <div class='return' @click='retreat()'></div>
            <span  class='Title'>交易中心</span>
            <div class='Usdt'>
                <select v-model="take">
                    <option v-for='(item,index) in takes' :key='index'>{{item}}</option>
                </select>
            </div>
        </div>
        <div class='mine_cen'>
            <div class='user_bot'>
                <div @click='skip(1)' :class='{active:showIndex === 1}' style='border-right:3px solid whitesmoke;'>
                    <p>出售</p>
                </div>
                <div @click='skip(2)' :class='{active:showIndex === 2}'>    
                    <p>求购</p>
                </div>
            </div> 
            <div class='news'>  
                <p>
                    今日HAIFUN  |  
                    <em>今日成交额 9.999.999</em>
                </p>
            </div>
            <components :is='Skip' :take='take'></components>
        </div>
        <Tab :Index="Index"></Tab>
    </div>
</template>

<script>

import buy from '../components/Mines/Deal/buy';
import sell from '../components/Mines/Deal/sell';
import Tab from '../components/Tab'

export default {
    name: 'deal',
    components:{ buy,sell,Tab },
    data() {
        return {
            Skip:sell,
            showIndex:1,
            take:'CNY',
            takes:'',
            Index:3
        }
    },
    created(){
        this.Currency();
    },
    methods:{
        retreat(){
            this.$router.go(-1);  
        },
        skip(index){
            this.showIndex = index;
            if( index == 1 ){
                this.Skip = sell;
            }else if( index == 2 ){
                this.Skip = buy;
            }
        },
        Currency(){
            let url = '/blockchain/getSupportCoins';
            this.$http.post(url,{
                cmd:'',
                body:{}
            }).then( (res) => {
                this.takes = res;
                // console.log(this.takes)
            })
        }
    },
}
</script>

